<template>
    <div class="dashBoard">
        <el-container>
            <el-header height="60px">
                <div class="head_left">
                    <i class="iconfont item_logo">&#xe7f7;</i>
                    <span>上门洗车后台管理系统</span>
                </div>
                <div class="head_right">
                    <div class="admin_name" @click="isShowUm = !isShowUm">
                        <i class="iconfont admin_logo">&#xe817;</i>
                        <span>administrator</span>
                        <i class="iconfont down_logo">&#xe843;</i>
                    </div>
                    <div class="exit_logo" @click="exit()">
                        <i class="iconfont">&#xe774;</i>
                    </div>
                </div>
            </el-header>
            <el-collapse-transition>
                <div class="user_manual" v-show="isShowUm">
                    <div class="um1">
                        <i class="iconfont">&#xe7f9;</i>编辑资料
                    </div>
                    <div class="um2">
                        <i class="iconfont">&#xe862;</i>操作日志
                    </div>
                    <div class="um3">
                        <i class="iconfont">&#xe7fa;</i>修改密码
                    </div>
                </div>
            </el-collapse-transition>
            <el-container>
                <el-aside width="250px">
                    <div
                        class="aside_menu"
                        :class="{active:item.id==isShowMenu}"
                        v-for="item in navMenu"
                        :key="item.id"
                    >
                        <div class="main_menu" @click="showMenu(item.id)">
                            <div class="menu_name">
                                <i class="iconfont">&#xe818;</i>
                                <div>{{item.name}}</div>
                            </div>
                        </div>
                    </div>
                </el-aside>
                <el-container>
                    <el-main>
                        <router-view/>
                    </el-main>
                    <el-footer height="60px">
                        备案号:鲁ICP备18033902号
