                    </el-footer>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>

<script>
export default {
    name: "dashBoard",
    data() {
        return {
            navMenu: [
                {
                    id: 0,
                    name: "订单列表"
                }
            ],
            isShowMenu: null,
            isShowUm: false
        };
    },
    created() {},
    methods: {
        // 显示/隐藏菜单
        showMenu(id) {
            if (this.isShowMenu == id) {
                this.isShowMenu = null;
            } else {
                this.isShowMenu = id;
            }
        },
        exit() {
            this.$confirm("您确定要现在退出系统吗?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                center: true
            })
                .then(() => {
                    localStorage.removeItem("token");
                    this.$message({
                        type: "success",
                        message: "已成功退出！"
                    });
                    this.$router.push({ path: "/login" });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消退出！"
                    });
                });
        }
    }
};
</script>

