import Vue from 'vue'
import router from '../router'
//引入axios
import axios from 'axios'
//axios配置
Vue.prototype.$axios = axios

//配置默认axios参数
//开发 测试环境
if (process.env.NODE_ENV === "production") {
    axios.defaults.baseURL = `http://wash.carbycar.cn/`;
}
if (process.env.NODE_ENV === "development") {
    axios.defaults.baseURL = `http://192.168.40.234:8011/`;
}
axios.defaults.timeout = 30000
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'

//添加请求拦截器,发送axios请求前执行
axios.interceptors.request.use((config) => {
    const token = localStorage.getItem('token')
    // console.log('token', token);
    if (!token && router.currentRoute.path != '/login') {//本地无token,当前路径不为登录跳转至登录页面
        router.push('/login')
    } else {
        config.headers.Authorization = "Bearer "+token;
    }
    return config
}, function (error) {
    alert(error)
    return Promise.reject(error)
})

//返回结果拦截
axios.interceptors.response.use((response) => {
    if (response.data.code == 200) {//成功
        return Promise.resolve(response)
    } else {
        // console.log(response.data);
        Vue.prototype.$message.error(response.data.message)
        if(response.data.code == 401){//token失效
            router.push('/login')
        }
    }
}, function (error) {
    //请求错误时做些事
    return Promise.reject(error)
})
