import Vue from 'vue'
import Router from 'vue-router'
import dashBoard from "./views/dashBoard"
import login from './views/login'
import home from './components/home/home'

Vue.use(Router)

let routes = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/dashBoard/home'
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/dashBoard',
      name: 'dashBoard',
      component: dashBoard,
      children: [
        {
          path: 'home',
          name: 'home',
          component: home
        }
      ]
    }
  ]
})

export default routes
