<template>
    <div class="login" :class="bg">
        <div class="login_card">
            <div class="card_body">
                <div class="card_title">登录</div>
                <div class="input_box">
                    <div>
                        用户名
                        <span>*</span>
                    </div>
                    <el-input v-model="unInput" clearable @keyup.enter.native="login"></el-input>
                </div>
                <div class="input_box">
                    <div>
                        密码
                        <span>*</span>
                    </div>
                    <el-input v-model="pwInput" show-password @keyup.enter.native="login"></el-input>
                </div>
                <div class="check_box">
                    <el-checkbox v-model="rememberPassword">记住密码</el-checkbox>
                    <div class="forget">忘记密码</div>
                </div>
                <button class="login_btn" @click="login()">登录</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "login",
    data() {
        return {
            bg: "",
            unInput: "",
            pwInput: "",
            codeImg: "",
            uuid: "",
            rememberPassword: true
        };
    },
    created() {
        this.accessPage();
    },
    mounted() {
        this.loadAccountInfo();
    },
    methods: {
        accessPage() {
            // 生成随机数，修改登录背景
            let i = Math.ceil(Math.random() * 3);
            this.bg = "bg" + i;
        },
        // 登录
        login() {
            //记住密码checkbox的勾选状态 和账号信息的字符串
            const username = this.unInput;
            const password = this.pwInput;
            const rememberStatus = this.rememberPassword;
            let accountInfo = "";
            accountInfo = username + "&" + password;

            if (this.unInput == "") {
                this.$message({
                    message: "用户名不能为空！",
                    type: "warning"
                });
                return;
            }
            if (this.pwInput == "") {
                this.$message({
                    message: "密码不能为空！",
                    type: "warning"
                });
                return;
            }
            this.$axios
                .post("user/login", {
                    username: username,
                    password: password
                })
                .then(res => {
                    // console.log("登录：", res.data);
                    if (res.data.code == 200) {
                        if (rememberStatus) {
                            // console.log("勾选了记住密码，现在开始写入cookie");
                            this.$cookies.set("accountInfo", accountInfo);
                        } else {
                            // console.log("没有勾选记住密码，现在开始删除账号cookie");
                            this.$cookies.remove("accountInfo");
                        }
                        localStorage.setItem("token", res.data.data);
                        this.$router.push({ path: "/" });
                    }
                })
        },
        // 判断cookie里没有账号信息
        loadAccountInfo() {
            let accountInfo = this.$cookies.get("accountInfo");
            //如果cookie里没有账号信息
            if (Boolean(accountInfo) == false) {
                // console.log("cookie中没有检测到账号信息！");
                return false;
            } else {
                //如果cookie里有账号信息
                // console.log("cookie中检测到账号信息！现在开始预填写！");
                let username = "";
                let password = "";
                let index = accountInfo.indexOf("&");

                username = accountInfo.substring(0, index);
                password = accountInfo.substring(index + 1);

                this.unInput = username;
                this.pwInput = password;
                this.rememberPassword = true;
            }
        }
    }
};
</script>

