<template>
    <div class="sysMenu">
        <!-- 菜单管理 -->
        <div class="header">订单列表</div>
        <div class>
            <el-input class="Input" placeholder="请输入订单号" v-model="orderNo"></el-input>
            <el-select class="Input" v-model="state" placeholder="请选择订单状态">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                ></el-option>
            </el-select>
            <el-date-picker
                class="Input"
                v-model="startTime"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择开始日期时间"
                align="right"
                :picker-options="pickerOptions"
            ></el-date-picker>
            <el-date-picker
                class="Input"
                v-model="endTime"
                type="datetime"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择结束日期时间"
                align="right"
                :picker-options="pickerOptions"
            ></el-date-picker>
            <el-button @click="getOrderList()">查询</el-button>
        </div>
        <div class="table_box">
            <el-table
                :data="orderList"
                style="width: 100%"
                border
                row-key="menuId"
                :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
            >
                <el-table-column align="center" prop="licence" label="车牌号" width="100"></el-table-column>
                <el-table-column align="center" prop="couponName" label="优惠券名称" width="120"></el-table-column>
                <el-table-column align="center" prop="orderNo" label="订单号" width="200"></el-table-column>
                <el-table-column align="center" prop="address" label="地址" width="300"></el-table-column>
                <el-table-column align="center" prop="appointTimeStart" label="预约时间开始" width="180"></el-table-column>
                <el-table-column align="center" prop="appointTimeEnd" label="预约时间结束" width="180"></el-table-column>
                <el-table-column align="center" prop="mobile" label="手机号" width="150"></el-table-column>
                <el-table-column align="center" prop="acceptName" label="接单人" width="100"></el-table-column>
                <el-table-column align="center" prop="orderPay" label="实际支付金额" width="150"></el-table-column>
                <el-table-column align="center" prop="itemName" label="项目名称" width="150"></el-table-column>
                <el-table-column align="center" prop="payType" label="付款类型" width="100"></el-table-column>
                <el-table-column align="center" prop="beforeWash" label="洗车前照片" width="100">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            @click="lookBefore(scope.$index, scope.row)"
                            v-show="scope.row.beforeWash!=null"
                        >查看</el-button>
                        <el-link type="info" disabled v-show="scope.row.beforeWash==null">暂无照片</el-link>
                    </template>
                </el-table-column>
                <el-table-column align="center" prop="afterWash" label="洗车后照片" width="100">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            @click="lookAfter(scope.$index, scope.row)"
                            v-show="scope.row.afterWash!=null"
                        >查看</el-button>
                        <el-link type="info" disabled v-show="scope.row.afterWash==null">暂无照片</el-link>
                    </template>
                </el-table-column>
                <el-table-column align="center" prop="orderPrice" label="订单金额" width="100"></el-table-column>
                <el-table-column align="center" prop="createName" label="创建人" width="150"></el-table-column>
                <el-table-column align="center" prop="createTime" label="创建时间" width="180"></el-table-column>
                <el-table-column align="center" prop="firstOrder" label="是否首单" width="100">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.firstOrder == '1' ? 'success' : 'danger'"
                            disable-transitions
                        >{{scope.row.firstOrder == '1'?'是':'否'}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column align="center" prop="state" label="订单状态" width="250">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.stateType"
                            disable-transitions
                        >{{scope.row.stateTxt}}</el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagination_box">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :hide-on-single-page="true"
                :page-size="10"
                layout="total, prev, pager, next, jumper"
                :total="orderTotal"
            ></el-pagination>
        </div>
        <div class="image_lazy" :class="{'hide':isHide}">
            <el-image v-for="url in urls" :key="url" :src="url" lazy></el-image>
        </div>
        <div class="bg" :class="{'hide':isHide}" @click="close()"></div>
    </div>
</template>

<script>
export default {
    name: "sysMenu",
    data() {
        return {
            currentPage: 1,
            orderPage: 1,
            orderTotal: 0,
            orderList: [],
            urls: [],
            isHide: true,
            options: [
                {
                    value: "0",
                    label: "订单已创建，待支付"
                },
                {
                    value: "1",
                    label: "已经付款等待上传洗车前照片"
                },
                {
                    value: "2",
                    label: "已洗车待上传洗车后照片"
                },
                {
                    value: "3",
                    label: "订单已取消"
                },
                {
                    value: "10",
                    label: "B端上传完洗完洗车照片订单完成"
                }
            ],
            pickerOptions: {
                shortcuts: [
                    {
                        text: "今天",
                        onClick(picker) {
                            picker.$emit("pick", new Date());
                        }
                    },
                    {
                        text: "昨天",
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit("pick", date);
                        }
                    },
                    {
                        text: "一周前",
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit("pick", date);
                        }
                    }
                ]
            },
            orderNo: "",
            state: "",
            startTime: "",
            endTime: ""
        };
    },
    created() {
        this.getOrderList();
    },
    methods: {
        // 订单列表：
        getOrderList() {
            this.$axios
                .post(
                    `back/washOrder/DYList?page=${
                        this.orderPage
                    }&limit=10&orderNo=${this.orderNo}&state=${
                        this.state
                    }&startTime=${this.startTime}&endTime=${this.endTime}`
                )
                .then(res => {
                    // console.log("订单列表：", res.data);
                    this.orderTotal = res.data.data.total;
                    this.orderList = res.data.data.list;
                    for (let i in this.orderList) {
                        this.orderList[i].acceptName = decodeURI(
                            this.orderList[i].acceptName
                        );
                        this.orderList[i].createName = decodeURI(
                            this.orderList[i].createName
                        );
                        this.orderList[i].appointTimeEnd = this.format(
                            this.orderList[i].appointTimeEnd
                        );
                        this.orderList[i].appointTimeStart = this.format(
                            this.orderList[i].appointTimeStart
                        );
                        this.orderList[i].createTime = this.format(
                            this.orderList[i].createTime
                        );
                        this.orderList[i].stateType = this.orderStateType(
                            this.orderList[i].state
                        );
                        this.orderList[i].stateTxt = this.orderStateTxt(
                            this.orderList[i].state
                        );
                    }
                });
        },
        add0(m) {
            return m < 10 ? "0" + m : m;
        },
        // 时间戳转化时间
        format(val) {
            const time = new Date(val);
            const y = time.getFullYear();
            const m = time.getMonth() + 1;
            const d = time.getDate();
            const h = time.getHours();
            const mm = time.getMinutes();
            const s = time.getSeconds();
            return (
                y +
                "-" +
                this.add0(m) +
                "-" +
                this.add0(d) +
                " " +
                this.add0(h) +
                ":" +
                this.add0(mm) +
                ":" +
                this.add0(s)
            );
        },
        // 判断订单状态
        orderStateType(val) {
            switch (val) {
                case "0":
                    return "";
                case "1":
                    return "warning";
                case "2":
                    return "warning";
                case "3":
                    return "info";
                case "10":
                    return "success";
                default:
                    return "danger";
            }
        },
        // 判断订单状态转成文字
        orderStateTxt(val) {
            switch (val) {
                case "0":
                    return "订单已创建，待支付";
                case "1":
                    return "已经付款等待上传洗车前照片";
                case "2":
                    return "已洗车待上传洗车后照片";
                case "3":
                    return "订单已取消";
                case "10":
                    return "B端上传完洗完洗车照片订单完成";
                default:
                    return "null";
            }
        },
        lookBefore(index, row) {
            this.isHide = false;
            let beforeWash = row.beforeWash.split(",");
            for (let i in beforeWash) {
                if (beforeWash[i] == "") {
                    beforeWash.splice(i, 1);
                }
            }
            this.urls = beforeWash;
        },
        lookAfter(index, row) {
            this.isHide = false;
            let afterWash = row.afterWash.split(",");
            for (let i in afterWash) {
                if (afterWash[i] == "") {
                    afterWash.splice(i, 1);
                }
            }
            this.urls = afterWash;
        },
        close() {
            this.isHide = true;
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
            this.orderPage = val;
            this.getOrderList();
        }
    }
};
</script>

<style lang="stylus">
.sysMenu {
    background: #fff;
    border-radius: 5px;
    padding: 20px;
    min-height: 85vh;

    .header {
        margin-bottom: 12px;
    }

    .Input {
        width: 220px;
        margin-right: 10px;
    }

    .el-button+.el-button {
        margin-left: 30px;
    }

    .table_box {
        margin: 12px 0;
    }

    .pagination_box {
        text-align: right;
    }

    .el-table .cell {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .image_lazy {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 768px;
        text-align: center;
        height: 768px;
        margin: auto;
        z-index: 999;
        background: #fff;
        overflow-y: auto;
    }

    .image_lazy.hide {
        display: none;
    }

    .bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        z-index: 99;
        width: 100%;
        height: 100vh;
        background: #333;
        opacity: 0.8;
    }

    .bg.hide {
        display: none;
    }
}
</style>